import { fetchGetFreeBoard, fetchGetPopularBoard } from 'api/main';
import { fetchGetToken } from 'api/token';
import Board from 'components/board';
import Carousel from 'components/carousel';
import Chat from 'components/chat';
import Container from 'components/common/Container';
import Header from 'components/header';
import { useEffect, useState } from 'react';
import styled from 'styled-components';

const restaurantData = [
  //       
  [
    { name: '11', rank: 1, category: 'new ' },
    { name: '12', rank: 2, category: 'new ' },
    { name: '13', rank: 3, category: 'new ' },
  ],
  //     
  // ...
  [
    { name: '21', rank: 1, category: ' ' },
    { name: '22', rank: 2, category: ' ' },
    { name: '23', rank: 3, category: ' ' },
  ],
  [
    { name: '31', rank: 1, category: 'Best  ' },
    { name: '32', rank: 2, category: 'Best  ' },
    { name: '33', rank: 3, category: 'Best  ' },
  ],
  [
    { name: '41', rank: 1, category: 'Event  ' },
    { name: '42', rank: 2, category: 'Event  ' },
    { name: '43', rank: 3, category: 'Event  ' },
  ],
];

const Home = () => {
  const [board, setBoard] = useState({
    freeBoard: [],
    popularBoard: [],
  });

  useEffect(() => {
    const fetchData = async () => {
      await fetchGetToken();
      const [freeBoard, popularBoard] = await Promise.all([
        fetchGetFreeBoard(),
        fetchGetPopularBoard(),
      ]);
      setBoard({
        freeBoard,
        popularBoard,
      });
    };
    fetchData();
  }, []);

  useEffect(() => {}, []);
  const boardField = [
    {
      title: '',
      board: board.freeBoard,
    },
    {
      title: '',
      board: board.popularBoard,
    },
  ];

  return (
    <>
      <Container>
        <Header />
        <main>
          <Carousel data={restaurantData} />
          <FeedBox>
            {boardField.map((board, idx) => (
              <Board key={idx} title={board.title} board={board.board} />
            ))}
            <Chat />
          </FeedBox>
        </main>
      </Container>
    </>
  );
};

const FeedBox = styled.section`
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  justify-content: space-between;
`;

export default Home;
